<template>
  <form
    class="search"
    :class="{ search_expanded: isSearchExpanded }"
    @submit.prevent="search"
  >
    <input
      v-model="query"
      ref="input"
      class="search__input"
      type="text"
      :placeholder="isMobile ? $t('SEARCH_MOBILE_PLACEHOLDER') : $t('SEARCH_PLACEHOLDER')"
    />
    <button class="search__button" type="submit">
      <img
        class="search__icon search__icon_desktop"
        src="@/assets/icon-search.svg"
        alt="Search"
      />
      <img
        @click.prevent="toggleSearch"
        class="search__icon search__icon_mobile"
        src="@/assets/icon-search-mobile.svg"
        alt="Search"
      />
    </button>
  </form>
</template>

<script>
export default {
  data() {
    return {
      query: '',
      isMobile: window.innerWidth <= 767 || window.innerHeight <= 767,
    };
  },
  computed: {
    isSearchExpanded() {
      return this.$store.getters.isSearchExpanded;
    },
  },
  methods: {
    toggleSearch() {
      if (!this.isSearchExpanded) {
        this.$store.commit('isSearchExpanded', true);
        this.$nextTick(() => this.$refs.input.focus());
      }
    },
    handleSearch(e) {
      if (e.path.some(el => typeof el.className === 'string' && el.className.split(' ').includes('search'))) {
        return;
      }
      this.$store.commit('isSearchExpanded', false);
    },
    search() {
      let query = {
        query: this.query,
      };
      if (/^\/search/.test(this.$route.path)) {
        query = {
          ...this.$route.query,
          ...query,
        };
      }
      this.$router.push({ path: '/search', query });
      this.query = '';
      this.$refs.input.blur();
      this.$store.commit('isSearchExpanded', false);
    },
  },
  mounted() {
    document.body.addEventListener('click', this.handleSearch);
  },
  destroyed() {
    document.body.removeEventListener('click', this.handleSearch);
  },
};
</script>

<style lang="scss" scoped>
