.search {
  width: 365px;
  position: relative;

  @include notebook {
    width: auto;
  }

  &_expanded {
    @include notebook {
      width: auto;
      flex-grow: 1;
      z-index: 10;
    }
  }

  &_expanded &__input {
    @include notebook {
      display: block;
    }
  }

  &_expanded &__button {
    position: absolute;
    margin: auto;
  }

  &__input,
  &__button {
    background-color: var(--tertiary-bg);
  }

  &__input {
    width: 100%;
    padding: 9px 30px 9px 10px;
    font-size: $text-primary-size;
    border-radius: 2px;
    border: 1px solid var(--tertiary-accent);
    font-weight: 500;
    color: var(--text-primary);

    @include notebook {
      display: none;
      font-size: 12px;
      height: 41px;
      -webkit-appearance: none;

      &:focus {
        border: 1px solid var(--tertiary-accent) !important;
      }
    }

    @include tablet {
      font-size: 16px;
    }

    &::placeholder {
      color: var(--text-primary);
      font-weight: 400;

      @include notebook {
        color: $text-secondary;
      }
    }
  }

  &__button {
    position: absolute;
    width: 16px;
    height: 16px;
    right: 8px;
    top: 2px;
    bottom: 0;
    margin: auto;

    @include notebook {
      background: none;
      position: static;
      margin-top: 4px;
      margin-right: 8px;
    }
  }

  &__icon {
    &_mobile {
      display: none;

      @include notebook {
        display: block;
        filter: var(--img-filter);
      }
    }

    &_desktop {
      @include notebook {
        display: none;
      }
    }
  }
}
</style>
